<template>
	<div class="uplog_box">
		<header>
			<Headnav head_left="" head_title="密码登录" head_right=" ">
				<router-link to="/cart" tag="span" slot="left"> < </router-link>>
			</Headnav>
		</header>
		<div class="uplog_cont">
			<div class="log_inp">
				账号: <input type="text" v-model="username">
			</div>
			<div class="log_pass">
				<p>密码: <input :type="value" v-model="password"></p>
				<p @click="showPass()">
					<van-switch size="20px" v-model="flag" active-color="#07c160" inactive-color="#f44"/>
				</p>
			</div>
			<div class="log_img">
				<p>验证码: <input type="text" v-model="sms"></p>
				<p><img :src="code" alt=""></p>
				<p><button @click="getData()">看不清换一张</button></p>
			</div>
			<div class="log_text">
				<p>温馨提示:未注册过的账号,登录时自动注册</p>
				<p>注册过的用户,可凭账号密码登录</p>
			</div>
			<div class="log_on">
				<van-button @click="getUser" type="primary" size="large">登录</van-button>
				<div>
					<p></p>
					<p @click="del">重置密码？</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapMutations} from 'vuex'
	import {Dialog} from 'vant'
	axios.defaults.withCredentials =true
	import Headnav from '../About/headNav'
	export default {
		name: "upLog",
		data(){
			return{
				username:'',
				password:'',
				sms:"",
				flag:true,
				code:'',
				value:'password',
				show:false,
				message:'',
				list:[]
			}
		},
		created(){
			//获取登录接口
			this.getData()
		},

		methods:{
			//密码框开关
			...mapMutations(['getlocalStorage']),
			showPass(){
				if(this.flag){
					this.value='password'
				}else{
					this.value='text'
				}
			},
			del(){
				this.password =''
			},
			getData(){
				//获取图片接口
				this.$axios.post('http://elm.cangdu.org/v1/captchas',{}).then((res)=>{
					this.code=res.data.code
				})
			},
			//点击获取数据判断账号是否正确，正确后登录
			getUser(){
				//获取接口
				this.$axios.post('http://elm.cangdu.org/v2/login',{
					username:this.username,
					password:this.password,
					captcha_code:this.sms
				}).then((res)=>{
					//判断输入框值是否正确
					this.list=res.data
					console.log(res)
					if(this.username==''&&this.password==''&&this.sms==''){
						Dialog({message:'账号不能为空'})
						Dialog({message:'密码不能为空'})
						Dialog({message:'验证码不正确'})
						this.getData()
					}else if(res.data.message=='密码错误'){
						Dialog({message:'密码错误'})
						this.getData()
					}else if(res.data.message=='验证码不正确'){
						Dialog({message:'验证码不正确'})
						this.getData()
					}else{
						//正确后跳转页面
						let obj ={
							avatar:res.data.avatar,
							username:res.data.username,
							userId:res.data.user_id,
							start:true
					}
					this.$store.commit('getData',obj)
						this.$router.push({
							path:'/user'
						})
					}
				})
			},
			gett(){
				this.getlocalStorage()
			}
		},
		components:{
			Headnav
		}
	}
</script>
